import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router'
import { AuthService } from '../services/auth.service';
import { FlashMessagesModule } from 'angular2-flash-messages'
import { FlashMessagesService } from 'angular2-flash-messages/module/flash-messages.service';
import { ValidateService } from '../services/validate.service';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  username:string;
  password: string;
   isAdmin:any;
  constructor(private auth:AuthService,
              private validate:ValidateService,
              private flash:FlashMessagesService,
              private router:Router) { }

  ngOnInit() {
  }

  login(){
    let user = {
      username:this.username,
      password:this.password
    }

    console.log('User:',user);

    if(!this.validate.validateEmail(user.username)){
      this.flash.show('Invalid email!!',{cssClass:"alert-danger",timeout:3000})
      return;
    }
    if(this.username===undefined || this.password===undefined){
      this.flash.show("Please fill all the fields",{cssClass:"alert-danger",timeout:3000});
      return;
    }

    this.auth.authenticateUser(user).subscribe((data)=>{
      console.log('Data:',data);
      if(data.success){
        this.auth.storeDetails(data.user,data.token);
        this.auth.setAdmin(data.user.isAdmin);
        this.flash.show('Successfully Logged in',{cssClass:'alert-success',timeout:3000});
        this.router.navigate(['/apply-leave'])
      }else if(data.msg==="User not found"){
        this.flash.show('User not registered!!',{cssClass:'alert-danger',timeout:3000});
        this.router.navigate(['/login'])
      }
      else{
        this.flash.show('Username and Password do not match',{cssClass:'alert-danger',timeout:3000});
        this.router.navigate(['/login'])
      }
    })

  }

}



