import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { Router,RouterModule } from '@angular/router'
import { FormsModule } from '@angular/forms'

import { AppComponent } from './app.component';
import { HeaderComponent } from './header/header.component';
import { RegisterComponent } from './register/register.component';
import { LoginComponent } from './login/login.component';
import { ApplyLeaveComponent } from './apply-leave/apply-leave.component';
import { ApproveLeaveComponent } from './approve-leave/approve-leave.component';
import { HomeComponent } from './home/home.component';
import { ValidateService } from './services/validate.service';
import { FlashMessagesService } from 'angular2-flash-messages/module/flash-messages.service';
import { FlashMessagesModule } from 'angular2-flash-messages/module/module';
import { AuthService } from './services/auth.service';
import { LeavesService } from './services/leaves.service'
import { HttpModule } from '@angular/http';
import { AuthGuardService} from './services/auth.guard.service'

const routesArray = [
                    {path:'',component:HomeComponent},  
                    {path:'register',component:RegisterComponent},
                     {path:'login',component:LoginComponent},
                      {path:'apply-leave',component:ApplyLeaveComponent,canActivate:[AuthGuardService]},
                      {path:'approve-leave',component:ApproveLeaveComponent,canActivate:[AuthGuardService]}]
@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    RegisterComponent,
    LoginComponent,
    ApplyLeaveComponent,
    ApproveLeaveComponent,
    HomeComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(routesArray),
    FormsModule,
    FlashMessagesModule,
    HttpModule
  ],
  providers: [ValidateService,FlashMessagesService, AuthService,LeavesService,AuthGuardService],
  bootstrap: [AppComponent]
})
export class AppModule { }



