import { Component, OnInit } from '@angular/core';
import { ValidateService } from '../services/validate.service';
import { FlashMessagesModule } from 'angular2-flash-messages'
import { FlashMessagesService } from 'angular2-flash-messages/module/flash-messages.service';
import { AuthService } from '../services/auth.service';
import { Router } from '@angular/router'

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  
  name:string;
  username:string;
  password:string;
  role:string;
  team:string;
  usersArr = [];

  constructor(private validate:ValidateService,
    private flash:FlashMessagesService,
    public auth:AuthService,
    private router:Router) { }

  ngOnInit() {
    this.auth.getUsers().subscribe(res=>this.usersArr = res);
  }

  registerUser(){
    let user={
      name:this.name,
      username:this.username,
      password:this.password,
      role:this.role,
      team:this.team
    }
    //let usersArr = [];
    let isUser:boolean;

    console.log('New User:',user);


    if(!this.validate.checkEmpty(user)){
      this.flash.show('One or more fields are Empty!!',{cssClass:"alert-danger",timeout:3000});
      return;
    } 

    if(!this.validate.validateEmail(user.username)){
      this.flash.show('Invalid email!!',{cssClass:"alert-danger",timeout:3000})
      return;
    }
      console.log("Users Array:",this.usersArr);
      this.usersArr.forEach(element=>{
        console.log("Users Array Element:",element);
        if(element.username===this.username){
          isUser=true;
          return;
        } 
        else isUser = false;
      })
      console.log("VAlue of isUser:",isUser);
      if(isUser) {
        this.flash.show("Username already exists,use another username",{cssClass:"alert-danger",timeout:3000})
        return;
      }
      // this.auth.checkUser(user);
    this.auth.registerUser(user).subscribe((data)=>{
      console.log("Data Posted:",data);
      if(data){
        this.flash.show('User Registered successfully!!',{cssClass:'alert-success',timeout:3000});
        this.router.navigate(['/login']);
      } else{
        this.flash.show('User Registration Failed!!',{cssClass:'alert-danger',timeout:3000});
      }
      
    });

    
    
    
  }

}



