import { Component, OnInit,ElementRef } from '@angular/core';
import { LeavesService } from '../services/leaves.service'
import { ViewChild } from '@angular/core/src/metadata/di';
import { FlashMessagesService } from 'angular2-flash-messages/module/flash-messages.service';

@Component({
  selector: 'app-approve-leave',
  templateUrl: './approve-leave.component.html',
  styleUrls: ['./approve-leave.component.css']
})
export class ApproveLeaveComponent implements OnInit {

  leavesApproveArray = [];
  filterArray = [];
  leaveId;
  leaveToApprove;
  leaveStatus;
  leaveComments;
  showForm;
  rowId;
  sortDate:string;
  filterBy:string;
  filterByValue:string;
  filterTo:string;
  filterFrom:string;
  filter:string;
  user = JSON.parse(localStorage.getItem('user'));
  constructor(private leaveService: LeavesService,private flash:FlashMessagesService) { }

  ngOnInit() {
    this.showApprovalLeaves();
  }

  formatDate(date){
    let arr=date.split('-')
    return (arr[2]+'-'+arr[1]+'-'+arr[0])
  }

  showApprovalLeaves() {
    let tempArr = []; 

    this.leaveService.showLeavesToApprove(this.user.team).subscribe((data)=>{
      //console.log(typeof data);
    for (const key in data) {
      if (data.hasOwnProperty(key)) {
        const element = data[key];
        console.log('Data Element:',element);
        //console.log(name);
        if(element.username!==this.user.username && element.team===this.user.team){
          console.log('Valid');
          this.leavesApproveArray.push(element);
        }
      }
    }
   });
  }

  enableApproveLeave(leave,i){
    this.showForm=1
    if(leave){
      //console.log('Enable Leave Clicked!!',i+1);
     // document.getElementById('leaveTable').deleteRow(i);
      this.leaveToApprove=leave;
      this.rowId=i;
      console.log(this.leaveToApprove);

    }
  }

  approveLeaves(id){ 
    //console.log(this.leaveStatusApproved)
   
     let details = {
       id:id,
       status:this.leaveStatus+'d',
       comment:this.leaveComments
     }  

     if(this.leaveStatus===undefined){
      this.flash.show("Please fill all empty fields!!",{cssClass:"alert-danger",timeout:3000}) 
      return;
     }
   
    this.leaveService.approveLeaves(details).subscribe((data)=>{
        this.leavesApproveArray.splice(this.rowId,1);
     });
     this.showForm=0;
   }

   sortInAscending(arr,option){
    if(option==='to'){
      arr.sort((a,b)=>{
        if(a.to<b.to){
          return -1;
        }else{
          return +1;
        }
      })
    }else{
      arr.sort((a,b)=>{
        if(a.from<b.from){
          return -1;
        }else{
          return +1;
        }
      })
    }
    
  }

  sortInDescending(arr,option){
    if(option==='to'){
      arr.sort((a,b)=>{
        if(a.to>b.to){
          return -1;
        }else{
          return +1;
        }
      })
    }else{
      arr.sort((a,b)=>{
        if(a.from>b.from){
          return -1;
        }else{
          return +1;
        }
      })
    }
    
  }
  sortByDate(arr,option){
    
    switch(option){
      case 'To Date: high to low':{
       this.sortInDescending(arr,'to');
       break;
      }
      case 'To Date: low to high':{
       this.sortInAscending(arr,'to');
       break;
      }
      case 'From Date: high to low':{
       this.sortInDescending(arr,'from');
       break;
      }
      case 'From Date: low to high':{
       this.sortInAscending(arr,'from');
       break;
      }
      default:{
        return;
      }
    }

    
  }

  fillFilterArray(filterBy){
    this.filterArray=[];
    this.leavesApproveArray.forEach((value)=>{
      console.log("Leaves Array Element:",value[filterBy.toLowerCase()]);
      if(this.filterArray.length===0){
        console.log("Inside If!!!")
        this.filterArray.push(value[filterBy.toLowerCase()]);
      }else{
        this.filterArray.forEach(ele=>{
          if(ele!==value[filterBy.toLowerCase()]){
            this.filterArray.push(value[filterBy.toLowerCase()])
          }
        })
      }
    })
    console.log('Filter Array:',this.filterArray);
  }

  filterValueBy(filterBy,value){
    console.log('Button CLicked!!',value);
    let arrayForFilter = [];
    this.leavesApproveArray.forEach((leave)=>{
      console.log(leave);
      //return (leave[filterBy.toLowerCase()]===value);
      if(leave[filterBy.toLowerCase()]===value){
        arrayForFilter.push(leave);
      }
    })
    console.log(arrayForFilter);
    this.leavesApproveArray = arrayForFilter;
  }

  filterByDate(from,to){
    let filterByDateArray = []
    console.log("Filter By Date",typeof from,to);
    this.leavesApproveArray.forEach(leave=>{
      console.log("Inside Filter by date:",leave);
      console.log(this.formatDate(this.filterFrom)<=leave.from)
      if(this.formatDate(this.filterFrom)<=leave.from){
      if(this.formatDate(this.filterTo)>=leave.to){
        console.log("Inside If",this.filterTo,leave.to);
        filterByDateArray.push(leave);
      }
    }
    })
    this.leavesApproveArray = filterByDateArray;
  }

  removeFilter(){
    this.showApprovalLeaves();
  }

}



