import { Component, OnInit } from '@angular/core';
import { LeavesService } from '../services/leaves.service';
import { FlashMessagesService } from 'angular2-flash-messages/module/flash-messages.service';

@Component({
  selector: 'app-apply-leave',
  templateUrl: './apply-leave.component.html',
  styleUrls: ['./apply-leave.component.css']
})
export class ApplyLeaveComponent implements OnInit {

  leavesArray = [];
  filterArray = [];
  from:string;
  to:string;
  sortDate:string;
  filterBy:string;
  filterByValue:string;
  filterTo:string;
  filterFrom:string;
  filter:string;

  constructor(private leaves:LeavesService,private flash:FlashMessagesService) { }

  ngOnInit() {
    this.showLeaves();
  }

  formatDate(date){
    let arr=date.split('-')
    return (arr[2]+'-'+arr[1]+'-'+arr[0])
  }

  validateDate(){
    if(this.from >this.to){
      this.flash.show('From date must be less than To date',{cssClass:'alert-danger',timeout:3000});
      return;
    }
  }

  applyLeave(){
    let userDetails = localStorage.getItem('user');
    //userDetails = JSON.parse(userDetails)
    //console.log('User Details',JSON.parse(userDetails).name);
    
    let leave={
      name:JSON.parse(userDetails).name,
      team:JSON.parse(userDetails).team,
      role:JSON.parse(userDetails).role,
      from:this.formatDate(this.from),
      to:this.formatDate(this.to),
      status:'Pending'
    }
      this.leaves.applyLeave(leave).subscribe((data)=>{
      console.log(data);
      this.leavesArray.push(leave);
    })
    
  }

  showLeaves(){
    console.log('Show Leaves Called!!!');
    this.leaves.showLeaves().subscribe((data)=>{
      //console.log('TYpe of data:',typeof data)
      console.log('Leaves data:',data);

      this.leavesArray = data;
    });
  }

  
  sortInAscending(arr,option){
    if(option==='to'){
      arr.sort((a,b)=>{
        if(a.to<b.to){
          return -1;
        }else{
          return +1;
        }
      })
    }else{
      arr.sort((a,b)=>{
        if(a.from<b.from){
          return -1;
        }else{
          return +1;
        }
      })
    }
    
  }

  sortInDescending(arr,option){
    if(option==='to'){
      arr.sort((a,b)=>{
        if(a.to>b.to){
          return -1;
        }else{
          return +1;
        }
      })
    }else{
      arr.sort((a,b)=>{
        if(a.from>b.from){
          return -1;
        }else{
          return +1;
        }
      })
    }
    
  }
  sortByDate(arr,option){
    
    switch(option){
      case 'To Date: high to low':{
       this.sortInDescending(arr,'to');
       break;
      }
      case 'To Date: low to high':{
       this.sortInAscending(arr,'to');
       break;
      }
      case 'From Date: high to low':{
       this.sortInDescending(arr,'from');
       break;
      }
      case 'From Date: low to high':{
       this.sortInAscending(arr,'from');
       break;
      }
      default:{
        return;
      }
    }

    
  }

  fillFilterArray(filterBy){
    this.filterArray=[];
    this.leavesArray.forEach((value)=>{
      console.log("Leaves Array Element:",value[filterBy.toLowerCase()]);
      if(this.filterArray.length===0){
        console.log("Inside If!!!")
        this.filterArray.push(value[filterBy.toLowerCase()]);
      }else{
        this.filterArray.forEach(ele=>{
          if(ele!==value[filterBy.toLowerCase()]){
            this.filterArray.push(value[filterBy.toLowerCase()])
          }
        })
      }
    })
    console.log('Filter Array:',this.filterArray);
  }

  filterValueBy(filterBy,value){
    console.log('Button CLicked!!',value);
    let arrayForFilter = [];
    this.leavesArray.forEach((leave)=>{
      console.log(leave);
      //return (leave[filterBy.toLowerCase()]===value);
      if(leave[filterBy.toLowerCase()]===value){
        arrayForFilter.push(leave);
      }
    })
    console.log(arrayForFilter);
    this.leavesArray = arrayForFilter;
  }

  filterByDate(from,to){
    let filterByDateArray = []
    console.log("Filter By Date",typeof from,to);
    this.leavesArray.forEach(leave=>{
      console.log("Inside Filter by date:",leave);
      console.log(this.formatDate(this.filterFrom)<=leave.from)
      if(this.formatDate(this.filterFrom)<=leave.from){
      if(this.formatDate(this.filterTo)>=leave.to){
        console.log("Inside If",this.filterTo,leave.to);
        filterByDateArray.push(leave);
      }
    }
    })
    this.leavesArray = filterByDateArray;
  }

  removeFilter(){
    this.showLeaves();
  }

  /*fillFilterArray(option){
    var newArray = [];
    var lookupObject  = {};

    for(var i in this.leavesArray) {
       lookupObject[this.leavesArray[i][option.toLowerCase()]] = this.leavesArray[i];
    }

    for(i in lookupObject) {
      this.filterArray.push(lookupObject[i]);
    }

    console.log('New Array:',newArray);
    
  }*/

  
}



