import { Injectable } from '@angular/core';
import {HttpModule,Http,Headers} from '@angular/http';
import 'rxjs/add/operator/map';
import {tokenNotExpired} from 'angular2-jwt'

@Injectable()
export class AuthService {

  authToken:string;
  user:any;
  isAdmin:number;

  constructor(private http:Http ) { }

  getUsers(){
    const headers = new Headers();
    headers.append('Content-Type','application/json');
    return this.http.get('/user/register',{headers:headers})
                    .map(res=>res.json()) 
  }

  checkUser(user){
    console.log("Inside checkUser");
     this.getUsers().subscribe(data=>{
       console.log("DAta:",data);
       
     })
  }

 
  registerUser(user){

    
    const headers = new Headers();
    headers.append('Content-Type','application/json');
    return this.http.post('/user/register',user,{headers:headers})
                    .map(res=>res.json());         
  }

  authenticateUser(user){
    const headers = new Headers();
    headers.append('Content-Type','application/json');
    return this.http.post('/user/authenticate',user,{headers:headers})
                    .map(res=>res.json());         
  }

  storeDetails(user,token){
    localStorage.setItem('token_id',token);
    localStorage.setItem('user',JSON.stringify(user));
    this.authToken = token;
    this.user = user;
    console.log('Value of User:',user);
    
  }

  isLoggedIn(){
    return tokenNotExpired('token_id');
  }

  setAdmin(admin){
    this.isAdmin = admin;
  }

  getAdmin(){
    return this.isAdmin;
  }

  logOut(){
    this.authToken=null;
    this.user=null;
    localStorage.clear();
  }

}



